import type { LumiClient } from '@/core/lumi-client'
import type { ApiResponse, ApiStreamResponse } from '@/lib/request'
import { EventSourceParserStream } from 'eventsource-parser/stream'
import { LumiError } from '@/lib/error'
import { requestStream } from '@/lib/request'
import { request } from '@/lib/request'
import { delay, getEnv, isServer } from '@/utils/common'

export interface MessageMedia {
  mimeType: string
  url: string
}

export interface Message {
  role: 'system' | 'user' | 'assistant'
  content: string
  medias?: MessageMedia[]
}

export interface GenerateTextResult {
  chatId: string
  content: string
}

export interface GenerateImageResult {
  chatId: string
  messageId: string
  generationStatus: 'PROCESSING' | 'SUCCESS' | 'FAILED'
  content?: string
  imageURL?: string
}

export interface StreamChunk<T> {
  event?: string
  data: T
}

export class AITool {
  readonly #lumi: LumiClient

  constructor(lumi: LumiClient) {
    this.#lumi = lumi
  }

  /** AI  */
  public async generateText({
    model = 'gemini-2.5-flash',
    messages,
  }: {
    model?: string
    messages: Message[]
  }): Promise<GenerateTextResult> {
    const lumiApiKey = this.checkLumiApiKey()

    const res = await request<ApiResponse<GenerateTextResult>>(this.#lumi, `/lm/${this.#lumi.config.projectId}/ai/chat/completions`, {
      method: 'POST',
      body: {
        lumiApiKey,
        modelName: model,
        chatMessages: messages,
      },
    })

    if (res.code !== 200)
      throw new LumiError(res.code, res.message)

    return res.data
  }

  /** AI  */
  public async generateTextStream({
    model = 'gemini-2.5-flash',
    messages,
  }: {
    model?: string
    messages: Message[]
  }): Promise<ApiStreamResponse> {
    const lumiApiKey = this.checkLumiApiKey()

    return requestStream(this.#lumi, `/lm/${this.#lumi.config.projectId}/ai/chat/stream`, {
      method: 'POST',
      body: {
        lumiApiKey,
        modelName: model,
        chatMessages: messages,
      },
    })
  }

  /** AI  */
  public async generateImage({
    model = 'gemini-2.5-flash-image',
    messages,
  }: {
    model?: string
    messages: Message[]
  }): Promise<GenerateImageResult> {
    const lumiApiKey = this.checkLumiApiKey()

    const { code, message, data } = await request<ApiResponse<GenerateImageResult>>(this.#lumi, `/lm/${this.#lumi.config.projectId}/ai/image/task`, {
      method: 'POST',
      body: {
        lumiApiKey,
        modelName: model,
        chatMessages: messages,
      },
    })
    if (code !== 200)
      throw new LumiError(code, message)

    let result = data
    let delayMs = 1000

    // 
    while (result.generationStatus === 'PROCESSING') {
      await delay(delayMs)
      if (delayMs < 5000)
        delayMs += 500

      const { code, message, data } = await request<ApiResponse<GenerateImageResult>>(this.#lumi, `/lm/${this.#lumi.config.projectId}/ai/image/get`, {
        method: 'POST',
        body: {
          lumiApiKey,
          messageId: result.messageId,
        },
      })
      if (code !== 200)
        throw new LumiError(code, message)
      result = data
    }

    return result
  }

  /**  SSE  GenerateTextResult  */
  public async parseStream(stream: ApiStreamResponse): Promise<ReadableStream<StreamChunk<GenerateTextResult>>> {
    return stream
      .pipeThrough(new TextDecoderStream())
      .pipeThrough(new EventSourceParserStream())
      .pipeThrough<StreamChunk<GenerateTextResult>>(new TransformStream({
        transform: (chunk, controller) => {
          try {
            const data = JSON.parse(chunk.data)
            controller.enqueue({
              event: chunk.event,
              data,
            })
          }
          catch {
            console.error(`Parse stream chunk failed: Invalid JSON.\n${chunk.data}`)
          }
        },
      }))
  }

  private checkLumiApiKey(): string {
    if (!isServer)
      throw new LumiError(400, 'lumi.tools.ai is only available on the server-side')

    const lumiApiKey = getEnv('LUMI_API_KEY')
    if (!lumiApiKey)
      throw new LumiError(400, 'LUMI_API_KEY is required')

    return lumiApiKey
  }
}
