export function getIcon(): string | null {
  return document.querySelector<HTMLLinkElement>('link[rel="icon"]')?.href ?? null
}

export function getTitle(): string | null {
  return document.title ?? null
}

export const isServer = typeof window === 'undefined'

export function delay(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms))
}

export function getEnv(key: string): string | null {
  return typeof process !== 'undefined' ? process?.env[key] ?? null : null
}
