import type { FetchOptions, ResponseType } from 'ofetch'
import type { LumiClient } from '@/core/lumi-client'
import Base64 from 'crypto-js/enc-base64'
import Hex from 'crypto-js/enc-hex'
import HmacSHA256 from 'crypto-js/hmac-sha256'
import SHA256 from 'crypto-js/sha256'
import hash from 'object-hash'
import { ofetch } from 'ofetch'
import { LumiError } from '@/lib/error'
import { isServer } from '@/utils/common'

const SECRET_KEY = '6QrJZ7pFCmBZAeIJF7IArvkCz+EtzA0RVcpHkiQIsQyhs7QtCS9P+CueZdHfB2OtJcgX3BbqY9pfpWeAVTqCwQ=='

/** RFC 3986 compliant URI encoding */
function rfc3986Encode(str: string): string {
  return encodeURIComponent(str).replace(/[!'()*]/g, c => `%${c.charCodeAt(0).toString(16).toUpperCase()}`)
}

/**  */
function addSignature<T extends ResponseType>(options: FetchOptions<T>): void {
  // 
  const timestamp = Math.floor(Date.now() / 1000).toString()
  const nonce = Math.random().toString(36).substring(2, 15)

  // 
  const queryParams: Record<string, string> = { ...options.query }
  const canonicalQueryString = Object.keys(queryParams).sort().map(key => `${rfc3986Encode(key)}=${rfc3986Encode(String(queryParams[key]))}`).join('&')

  // 
  const headersToSign: Record<string, string> = {
    'x-timestamp': timestamp,
    'x-nonce': nonce,
  }
  const canonicalHeaders = Object.keys(headersToSign).sort().map(key => `${key}:${headersToSign[key]}`).join('\n')

  // 
  const payload = (options.body && !(options.body instanceof FormData)) ? JSON.stringify(options.body) : ''
  const hashedPayload = SHA256(payload).toString(Hex)

  // 
  const canonicalRequest = [canonicalQueryString, canonicalHeaders, hashedPayload].join('\n')

  // 
  const signature = Base64.stringify(HmacSHA256(canonicalRequest, SECRET_KEY))

  // 
  const headers = new Headers(options.headers)
  Object.entries(headersToSign).forEach(([key, value]) => {
    headers.set(key, value)
  })
  headers.set('X-Sign', signature)
  options.headers = headers
}

export interface ApiResponse<T> {
  code: number
  message: string
  data: T
}

export interface PaginationData<T> {
  total: number
  list: T[]
}

const requestMap = new Map<string, number[]>()

/** 4 */
function checkRateLimit<T extends ResponseType>(uri: string, options: FetchOptions<T>): void {
  const clonedOptions = { ...options }

  if (options.body instanceof FormData)
    clonedOptions.body = Array.from(options.body.entries())

  clonedOptions.headers = undefined

  const requestHash = hash([uri, clonedOptions])
  const now = Date.now()
  const requestQueue = requestMap.get(requestHash)?.filter(time => now - time < 1000) || []
  if (requestQueue.length >= 4)
    throw new LumiError(429, 'Too many requests')
  requestQueue.push(now)
  requestMap.set(requestHash, requestQueue)
}

function beforeRequest<T extends ResponseType>(lumi: LumiClient, uri: string, options: FetchOptions<T>, stream: boolean = false): void {
  options.headers = new Headers(options.headers)

  // 
  if (lumi.auth.accessToken)
    options.headers.set('Authorization', `Bearer ${lumi.auth.accessToken}`)

  // 
  if (stream) {
    if (!options.headers.get('Accept'))
      options.headers.set('Accept', 'text/event-stream')
    if (!options.headers.get('Cache-Control'))
      options.headers.set('Cache-Control', 'no-cache')
    if (!options.headers.get('X-Accel-Buffering'))
      options.headers.set('X-Accel-Buffering', 'no')
  }

  // 
  checkRateLimit(uri, options)

  // 
  addSignature(options)
}

/**  */
export function request<T>(lumi: LumiClient, uri: string, options: FetchOptions<'json'> = {}): Promise<T> {
  beforeRequest(lumi, uri, options)

  const isAuthenticated = lumi.auth.isAuthenticated

  return ofetch<T>(uri, {
    baseURL: lumi.config.apiBaseUrl,
    ...options,
    onResponse: ({ response }) => {
      //  Token 
      if (!isServer && isAuthenticated && (response._data as ApiResponse<any>)?.code === 2100)
        lumi.auth.signOut()
    },
  })
}

export type ApiStreamResponse = ReadableStream<Uint8Array>

/**  */
export async function requestStream(lumi: LumiClient, uri: string, options: FetchOptions<'stream'> = {}): Promise<ApiStreamResponse> {
  beforeRequest(lumi, uri, options, true)

  const stream = await ofetch(uri, {
    baseURL: lumi.config.apiBaseUrl,
    ...options,
    responseType: 'stream',
  })

  return stream
}
